* {
  margin: 0
}

body {
  background-color: lightgray;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
 /* mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));  fading gradient */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.breakpoint__medium-up .home-left{flex:0.7 1;border-right:1px solid black;height:90%;display:flex;flex-direction:column;justify-content:center}.breakpoint__medium-up .home-right{flex:0.3 1;height:90%;display:flex;flex-direction:column;justify-content:center;text-align:right}.breakpoint__medium-up .home-option{color:black;font-size:40px;padding:20px 70px}.breakpoint__medium-up .home-intro{font-size:50px;padding:0 70px}.breakpoint__medium-up .home-option:hover{transition:0.5s;color:#f1b10e}.breakpoint__medium-up .home-resume{text-decoration:none;font-weight:bold}.breakpoint__small-down .home-intro{font-size:45px}.breakpoint__small-down .home-button{font-family:'Raleway';font-weight:bold;font-size:30px;text-transform:lowercase;background-color:lightgray;border:1px solid #f1b10e;color:#f1b10e}.breakpoint__small-down .home-button:hover{color:#f1b10e;background-color:#f1b10e;color:lightgray}.home{display:flex;height:100vh;align-items:center;color:#f1b10e;margin:0 30px;font-family:'Poppins', sans-serif}.home-nav{text-decoration:none}

.header {
  display: flex;
  align-items: center;
  height: 60px;
  border-bottom: 1px solid gray;
  font-family: 'Poppins', sans-serif;
}

.header-left {
  flex: 1 1;
}

.header-right {
  display: flex;
  justify-content: space-evenly;
}

.header-nav {
  text-decoration: none;
}

.header-options {
  padding: 0 20px;
  color: black;
  font-size: 20px;
}

.header-options:hover {
  transition: 0.5s;
  color: rgb(241, 177, 14);
}
.breakpoint__large-up .about{display:flex;padding:50px 200px;font-family:'Poppins', sans-serif}.breakpoint__large-up .about-right{flex:0.3 1;display:flex;justify-content:flex-end}.breakpoint__large-up .about-left{flex:0.7 1}.breakpoint__large-up img{width:400px;height:400px}.breakpoint__large-up .about-info{padding-right:50px}.breakpoint__medium-down .about{display:flex;flex-direction:column;align-items:center;font-family:'Poppins', sans-serif;padding:50px 50px}.breakpoint__medium-down .about-info{padding-top:50px}.breakpoint__medium-down img{width:350px;height:350px}

.breakpoint__large-up .project-image{min-width:600px;height:350px;object-fit:fill;border:1px solid black}.breakpoint__medium-down .project-image{height:225px;min-width:400px;object-fit:fill;border:1px solid black}.project-container:hover{filter:grayscale(100%);-webkit-filter:grayscale(100%);-webkit-transition:all 1s ease}.project-container{filter:grayscale(0%);filter:gray;-webkit-filter:grayscale(0%);filter:none;transition:1s ease}

.work-project{display:flex;flex-wrap:wrap;justify-content:center}

.breakpoint__large-up .contact{display:flex;font-family:'Poppins', sans-serif}.breakpoint__large-up .contact-left{flex:0.5 1;padding-top:100px}.breakpoint__large-up .contact-right{flex:0.5 1;display:flex;justify-content:flex-end;height:100vh}.breakpoint__large-up .contact-title{margin-bottom:20px;padding:0 50px;margin-left:50px;color:#f1b10e}.breakpoint__large-up .contact-info{margin-bottom:20px;padding:0 50px;margin-left:50px}.breakpoint__large-up .contact-form{display:flex;flex-direction:column;padding:0 50px;margin-left:50px}.breakpoint__medium-down .contact{display:flex;flex-direction:column;align-items:center}.breakpoint__medium-down .contact-info{margin-bottom:20px;width:75%}.breakpoint__medium-down .contact-title{padding:20px 0;color:#f1b10e}.breakpoint__medium-down .contact-form{display:flex;flex-direction:column;margin:50px 0;width:75%}.contact-form>input,textarea{background-color:lightgray;margin-bottom:10px;padding:10px;border:1px solid gray;font-family:'Courier New', Courier, monospace}.contact-box{font-size:17px}button{padding:10px;border:1px solid gray;background-color:lightgray;font-family:'Courier New', Courier, monospace}button:hover{background-color:#f1b10e;transition:0.5s;opacity:0.8}.contact-map-box{display:flex;flex-direction:column}.contact-soc{text-decoration:none}


